* {
    box-sizing: border-box;
}

body {
    margin: 0;
    min-height: 100vh;
}

.App {
    margin: 0 auto;
}

img {
    max-width: 100%;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
    margin: 0;
    font-weight: normal;
    padding: 0;
}

a {
    text-decoration: none;
    color: inherit;
    &:hover {
        text-decoration: underline;
    }
}

button {
    font-family: inherit;
    cursor: pointer;
    border: none;
    background-color: transparent;
    * {
        pointer-events: none;
    }
}

input {
    font-family: inherit;
    padding: 0.5em;
    border-radius: 5px;
    border: 2px solid grayscale($primary);
    &:focus-visible {
        border: 2px solid $primary;
        outline: none;
    }
}

ul,
ol {
    list-style: none;
    padding: 0;
    margin: 0;
}

/* MAC like scrollbar */
::-webkit-scrollbar {
    width: 8px;
    background-color: rgba(0, 0, 0, 0);
    -webkit-border-radius: 100px;
}

::-webkit-scrollbar:hover {
    background-color: rgba(0, 0, 0, 0.09);
}

::-webkit-scrollbar-thumb:vertical {
    background: rgba(0, 0, 0, 0.5);
    -webkit-border-radius: 100px;
}

::-webkit-scrollbar-thumb:vertical:active {
    -webkit-border-radius: 100px;
}
